import { Injectable } from '@nestjs/common';
import { InjectRepository } from '@nestjs/typeorm';
import { In, Repository } from 'typeorm';
import { Constancia } from '../entities/constancia.entity';


@Injectable()
export class ConstanciaService {
  constructor(
    @InjectRepository(Constancia, 'constancias')
    private readonly Constancia: Repository<Constancia>
  ) {}

  findAll() {
    return this.Constancia
      .createQueryBuilder('')
      .select('*')
     .getRawMany();
  }

 
}
